.Image img{
    width: 100%;
    height: auto;
}

.Image p{
    margin-left: 20px;
    margin-top: 20px;
    position: absolute;
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 33px;
    letter-spacing: 0.05em;
    color: #FFFFFF;
}

.Image p:nth-child(2){
    margin-top: 20px;
    transform: rotate(-90deg);
}

@media screen and (max-width: 980px){
    .Image p{
        font-size: 16px;
    }
}
