.FirstScreen{
    width: 100%;
    background: url("../../../img/Rectangle3.jpg");
}

.TopLine{
    padding-top: 20px;
    margin-right: auto;
    margin-left: auto;
    display: flex;
    justify-content: center;
    font-family: 'Open Sans Condensed', serif;
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    line-height: 25px;
    color: #FFFFFF;
}

.MainMenu{
    box-sizing: border-box;
    padding-right: 80px;
    display: inline-block;
}

.MainMenu p{
    cursor: pointer;
    margin-bottom: 5px;
    margin-right: 20px;
    display: inline-block;
}

.MainMenu p:hover{
    color: #D1A954;
}

.MainMenu p:nth-child(5){
    margin-right: 0;
}

.Phone{
    display: inline-block;
    border-bottom: 2px solid #D1A954;
}

.Phone p{
    cursor: pointer;
    margin-bottom: 0;
}

.Phone p:hover{
    color: #D1A954;
}

@media screen and (max-width: 980px){
    .TopLine{
        flex-direction: column;
        align-items: center;
    }
    .MainMenu{
        padding-right: 0;
    }
    .MainMenu p{
        font-size: 12px;
    }
}

@media screen and (max-width: 980px) {
    .TopLine{
        margin-left: 70px;
    }
}