.Projects p {
    margin-bottom: 100px;
    text-align: center;
    margin-top: 150px;
    font-family: 'Open Sans Condensed', serif;
    font-style: normal;
    font-weight: 300;
    font-size: 48px;
    line-height: 65px;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    color: #D1A954;
}

.Images{
    justify-content: space-around;
    display: flex;
}

.Projects{
    max-width: 1100px;
    margin-right: auto;
    margin-left: auto;
    width: 100%;
}

.Reiting{
    margin-top: 150px;
    display: flex;
    margin-left: auto;
    margin-right: auto;
    justify-content: center;
    width: 100%;
}

.Years p{
    text-align: right;
    font-family: 'Open Sans Condensed', serif;
    font-style: normal;
    font-weight: 700;
    font-size: 45px;
    line-height: 98px;
    letter-spacing: 0.1em;
    margin-bottom: 0;
    margin-top: 0;
}

.Years{
    margin-left: 40px;
    width: 40%;
}

.OnImage{
    align-items: center;
    height: 100%;
    display: flex;
    position: absolute;
}

.OnIMG img{
    margin-left: 10px;
    margin-right: 10px;
}

.OnIMG img:nth-child(1){
    margin-left: 50px;
}

.OnIMG p{
    font-size: 30px;
    margin-bottom: 0;
    margin-top: 30px;
    text-align: left;
    margin-left: 50px;
    width: 70%;
}

.Reiting{
    position: relative;
}

.Theater{
    height: auto;
    width: 80%;
}


@media screen and (max-width: 980px){
    .OnIMG p{
        line-height: 38px;
        font-size: 20px;

    }
    .OnIMG img{
        width: 18px;
    }
    .Years p{
        line-height: 48px;
        font-size: 30px;
    }
}