.Text{
    align-items: center;
    margin-left: 10px;
    color: #FFFFFF;
    width: 50%;
    height: auto;
}

.Text h1{
    margin-bottom: 50px;
    font-family: 'Open Sans Condensed', serif;
    font-style: normal;
    font-weight: 700;
    font-size: 64px;
    line-height: 87px;
    letter-spacing: 0.1em;
}

.Text p{
    margin-top: 0;
    font-family: 'Open Sans', serif;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;
    margin-bottom: 50px;
}

.Text button{
    height: 50px;
    width: 160px;
    background: #D1A954;
    border: none;
}

@media screen and (max-width: 980px){
    .Text{
        margin-left: auto;
        margin-right: auto;
    }
}