.About{
    margin-right: auto;
    margin-left: auto;
    width: 100%;
    max-width: 1100px;
}

.About p{
    margin-bottom: 100px;
    text-align: center;
    margin-top: 150px;
    font-family: 'Open Sans Condensed', serif;
    font-style: normal;
    font-weight: 300;
    font-size: 48px;
    line-height: 65px;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    color: #D1A954;
}

.AboutThomsing{
    margin-left: 10px;
    margin-right: 10px;
    display: flex;
    justify-content: center
}

.AboutText{
    margin-right: 10px;
    width: 40%;
}

.AboutText p:nth-child(1){
    color: #FFFFFF;
    font-family: 'Open Sans Condensed', serif;
    font-style: normal;
    font-weight: 700;
    font-size: 21px;
    line-height: 29px;
    letter-spacing: 0.1em;
    margin-top: 0;
    margin-bottom: 40px;
}

.AboutText p:nth-child(2){
    color: #FFFFFF;
    margin-bottom: 0;
    margin-top: 0;
    font-family: 'Open Sans', serif;
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 150%;
    text-align: left;
}

.AboutNumbers h4{
    margin-bottom: 40px;
    color: #FFFFFF;
    margin-top: 0;
    font-family: 'Open Sans Condensed', serif;
    font-style: normal;
    font-weight: 700;
    font-size: 21px;
    line-height: 29px;
    letter-spacing: 0.1em;
}

.AboutNumbers p{
    text-align: left;
    margin-top: 15px;
    margin-bottom: 28px;
    font-family: 'Open Sans Condensed', serif;
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 22px;
    letter-spacing: 0.05em;
}

.AboutNumbers{
    margin-right: 10px;
}

.Img{
    height: 100%;
}

@media screen and (max-width: 980px){
    .Img{
        display: none;
    }
    .AboutThomsing{
        justify-content: space-around;
    }
}

@media screen and (max-width: 684px) {
    .AboutThomsing{
        align-items: center;
        flex-direction: column;
    }
    .AboutThomsing div{
        width: 70%;
    }
    .AboutText{
        margin-bottom: 30px;
    }
    .AboutNumbers{
        margin-right: 0;
        display: flex;
        align-items: center;
        flex-direction: column;
    }
    .AboutNumbers h4{
        display: none;
    }
}