.Partners p{
    margin-bottom: 100px;
    text-align: center;
    margin-top: 150px;
    font-family: 'Open Sans Condensed', serif;
    font-style: normal;
    font-weight: 300;
    font-size: 48px;
    line-height: 65px;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    color: #D1A954;
}

.Partners{
    max-width: 1100px;
    margin-left: auto;
    margin-right: auto;
    width: 100%;
}

.IMG{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.IMG img{
    margin-left: 20px;
    margin-right: 20px;
    margin-top: 20px;
    width: 20%;
    height: auto;
}

@media screen and (max-width: 980px){
    .IMG div:nth-child(1) img:nth-child(4){
        display: none;
    }
    .IMG div:nth-child(2) img:nth-child(4){
        display: none;
    }

    .IMG div{
        display: flex;
        justify-content: center;
    }
    .IMG img{
        width: 25%;
    }
}
