.Contacts{
    width: 100%;
    max-width: 1100px;
    height: auto;
    margin-left: auto;
    margin-right: auto;
}

.ToBeDonator{
    margin-bottom: 100px;
    text-align: center;
    margin-top: 150px;
    font-family: 'Open Sans Condensed', serif;
    font-style: normal;
    font-weight: 300;
    font-size: 48px;
    line-height: 65px;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    color: #D1A954;
}

.ContactsInp{
    margin-left: 20px;
    display: flex;
    justify-content: center;
}

.PhoneEmail{
    margin-left: 20px;
    margin-right: 50px;
    width: 20%;
    padding-right: 10px;
}

.PhoneEmail p{
    margin-left: 0;
    font-family: 'Open Sans Condensed', serif;
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 22px;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    color: white;
}

.Form input{
    color: white;
    margin-bottom: 30px;
    margin-right: 50px;
    width: 180px;
    height: 35px;
    background: #444444;
    border: 1px solid #616161;
    box-sizing: border-box;
}

.Form{
    width: 100%;
    height: auto;
    display: flex;
}

.TextArea{
    align-items: center;
    margin-right: 20px;
    flex-direction: column;
    display: flex;
}

.TextArea textarea{
    margin-right: 10px;
    resize:none;
    color: white;
    max-width: 300px;
    max-height: 100px;
    background: #444444;
    border: 1px solid #616161;
    box-sizing: border-box;
}

.TextArea button{
    justify-content: center;
    color: #ffffff;
    font-family: 'Open Sans Condensed', serif;
    font-style: normal;
    font-weight: 700;
    font-size: 18px;
    line-height: 25px;
    display: flex;
    align-items: center;
    text-align: center;
    letter-spacing: 0.1em;
    margin-top: 40px;
    width: 150px;
    height: 50px;
    background: #D1A954;
    border: none;
}

@media screen and (max-width: 760px){
    .TextArea textarea{
        width: 200px;
        height: 100px;
    }
    .TextArea button{
        width: 100px;
        height: 30px;
        font-size: 14px;
    }
    .PhoneEmail p{
        font-size: 13px;
    }
    .Form input{
        width: 160px;
    }
}