.FirstScreen {
    margin-left: auto;
    margin-right: auto;
    width: 100%;
    height: auto;
    background: url("../../img/Rectangle3.jpg");
    background-size: cover;
}

.ABC {
    padding-bottom: 287px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 190px;
    width: 100%;
    height: auto;
    display: flex;
    align-items: center;
    justify-content: space-around
}

.Slider button {
    color: #ffffff;
    justify-content: center;
    font-family: 'Open Sans Condensed', serif;
    font-style: normal;
    font-weight: 700;
    font-size: 18px;
    line-height: 25px;
    display: flex;
    align-items: center;
    text-align: center;
    letter-spacing: 0.1em;
    width: 150px;
    height: 50px;
    background: #D1A954;
}

.Slider img {
    margin-bottom: 20px;
    width: 350px;
    height: 490px;
}

.Slider {
    flex-direction: column;
    align-items: center;
    display: flex;
    margin-right: 10px;
}

@media screen and (max-width: 980px){
    .Slider{
        display: none;
    }
}